import React, {useEffect, useState} from 'react'
import {Table, message, Pagination} from 'antd'
import {mainConfig} from '../../../config/mainConfig'
import Helper from '../../utils/Helper'
import {Tooltip, Button, DatePicker, Modal, Form, Input,Popconfirm} from 'antd'
import {MinusOutlined} from '@ant-design/icons'
import moment from 'moment'
import {Link} from 'react-router-dom'
import { hide } from '@popperjs/core'
let day = new Date()
let dateTodayes = moment(day).format('DD-MMM-YYYY')
const {RangePicker} = DatePicker
const manageTab=["BUYER","PRESET","RDS","OFFICEWATCHLIST","BUYERINTEL"]
const ViewWatchlist = () => {
  const [data, setData] = useState<any>([])
  const [isModalOpen, setIsModalOpen] = useState<any>(false)
  const [currentPage, setCurrentPage] = useState<any>(1)
  const [rowCnt, setrowCnt] = useState<any>(0)
  const [activeMenu,setActivemenu]=useState<any>(1)
  const [loader,setloader]=useState<any>(false)
  const [buttonLoader,setbuttonLoader]=useState<any>(false)
  const [sorter, setsorter] = useState<any>({
    columnKey: 'as_of_date',
    order: 'descend',
  })
  const [sorter2, setsorter2] = useState<any>({
    columnKey: 'ProductAC4',
    order: 'descend',
  })

  useEffect(() => {
    setloader(true)

    getBuyerWatchlist()
  }, [activeMenu,currentPage,sorter,sorter2])
  let token = ''
  const getBuyerWatchlist = async() => {
    setloader(true)
    await  fetch(`${mainConfig.MAIN_ENDPOINT}/api/${"get-watchlist/"}${activeMenu}/${currentPage}/${sorter.columnKey}/${sorter.order == 'ascend' ? 1 : 0}`, {
      method: 'get',
      headers: {
        Authorization: `Bearer ${token}`,
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
    })
      .then(function (response) {
       
        response.json().then(function (data) {
          if (response.status != 200) {
            setData([])
            setrowCnt(1)
          }
          if (data.success == true && data?.data?.products?.length>0) {
             setData(data?.data?.products)
            setrowCnt(data.data.rowCnt)
      setloader(false)


          }
        })
      })
      .catch(function (err) {
        console.log('Fetch Error :-S', err)
      })
    
        setloader(false)

    
  }
  const columns = [
    {
      title: 'Agg Code',
      dataIndex: 'ProductAC4',
      key: 'ProductAC4',
      sorter: true,

      width: 100,
    },
    {
      title: 'Spot Code',
      dataIndex: 'SPOTCode',
      key: 'SPOTCode',
      sorter: true,

      width: 100,
      render: (item: any, record: any) => {
  
        return (
          <Tooltip placement='topLeft' title={'View Historical Pricing'}>
            <Link
              to={{pathname: '/product-details', state: {prod_id: record.ProductAC4,spotid:record.SPOTCode, spotCode: item}}}
            >
            
              {item}
            </Link>
          </Tooltip>
        )
      },
    },
    {
      title: 'Description',
      dataIndex: 'ProductDesc',
      key: 'ProductDesc',
      sorter: true,

      width: 200,
    },

    {
      title: 'Pack Size',
      dataIndex: 'PackSize',
      key: 'PackSize',
      width: 100,
      render: (item: any) => {
        if (!item) {
          return <>-</>
        }
        return Helper.addZero(item)
      },
      sorter: true,
    },
    {
      title: 'Broken Rule',
      dataIndex: 'BrokenRule',
      key: 'BrokenRule',
      width: 200,
      hidden: activeMenu==7 ?false:true,
      render: (item: any) => {
        if (!item) {
          return <>-</>
        }
        return Helper.addZero(item)
      },
      sorter: true,
    },
    {
      title: 'Buyer Comment',
      dataIndex: 'buyer_comments',
      key: 'buyer_comments',
      width: 200,
      render: (item: any) => {
        if (!item) {
          return <>-</>
        }
        return Helper.addZero(item)
      },
      sorter: true,
    },
    {
      title: 'Supplier Comment',
      dataIndex: 'supplier_comments',
      key: 'supplier_comments',
      width: 200,
      render: (item: any) => {
        if (!item) {
          return <>-</>
        }
        return Helper.addZero(item)
      },
      sorter: true,
    },
    {
      title: 'Date',
      dataIndex: 'AsOfDate',
      key: 'AsOfDate',
      
      width: 100,
      hidden: activeMenu==2 || activeMenu==6?true:false,
      render: (item: any) => {
        if (!item) {
          return <>-</>
        }
        return  moment(item).format('DD-MMM-YYYY')
      },
      sorter: true,
    },
    {
      title: 'Day',
      dataIndex: 'Day',
      key: 'Day',
      
      width: 100,
      hidden:  activeMenu==6?false:true,
      render: (item: any) => {
        if (!item) {
          return <>-</>
        }
        return (Helper.capitalizeFirstLetter(item))
      },
      sorter: true,
    },

    {
      title: 'Action',
      dataIndex: 'ProductAC4',
      key: 'ProductAC4',

      fixed: 'right' as 'right',
      width: 100,
      hidden:activeMenu==7?true:false,

      render: (item: any, record: any) => {
        return (
          <Tooltip placement='topLeft' title={'Remove from watchlist'}>
            <Button icon={<MinusOutlined />} onClick={() => removeElement(record)}></Button>
          </Tooltip>
        )
      },
    },
  ].filter(item => !item.hidden);
  const removeElement = (data: any) => {
    fetch(`${mainConfig.MAIN_ENDPOINT}/api/remove-from-watchlist/${data.watchlist_id}`, {
      method: 'put',
      headers: {
        Authorization: `Bearer ${token}`,
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
    })
      .then(function (response) {
        if (response.status != 200) {
        }
        response.json().then(function (data) {
          if (data.success == true) {
            message.success(data.data)
            getBuyerWatchlist()
          }
        })
      })
      .catch(function (err) {
        console.log('Fetch Error :-S', err)
      })
  }
  const showModal = () => {
    setIsModalOpen(true)
  }

  const handleCancel = () => {
    setIsModalOpen(false)
  }
  const onFinish = (fieldsValue: any) => {
    setloader(true)
    let date1 = '',
      date2 = ''
    if (!fieldsValue.productcode && !fieldsValue.daterange) return
    if (fieldsValue.daterange) {
      date1 = moment(fieldsValue.daterange[0]).format('YYYY-MM-DD')
      date2 = moment(fieldsValue.daterange[1]).format('YYYY-MM-DD')
    }
    var payLoad = {
      prodcode: fieldsValue.productcode ? fieldsValue.productcode : '',
      sdate: date1 ? date1 : '',
      edate: date2 ? date2 : '',
    }
    fetch(`${mainConfig.MAIN_ENDPOINT}/api/search-watchlist/${activeMenu}/1/AsofDate/0`, {
      method: 'post',
      headers: {
        Authorization: `Bearer ${token}`,
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(payLoad),
    })
      .then(function (response) {
        if (response.status != 200) {
          setData([])
        }
        response.json().then(function (data) {
          if (data.success == true) {
            setData(data.data.products)
            setloader(false)
          }
        })
      })
      .catch(function (err) {
        console.log('Fetch Error :-S', err)
      })
    setIsModalOpen(false)
  }
  const handleNotification =async()=>{
    setbuttonLoader(true);
    let obj={
      list_type:activeMenu,
      type:2,
      dataCount:data.lenght
      
    }
    await  fetch(`${mainConfig.MAIN_ENDPOINT}/api/notify-user`, {
        method: 'post',
        headers: {
          Authorization: `Bearer ${token}`,
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
        body:JSON.stringify(obj)
      
      })
        .then(function (data) {
        
         
          data.json().then(function (data) {
            if (data.success == true) {
              message.success(data.message)
              getBuyerWatchlist()
            }
          })
         
        })
        .catch(function (error) {})
        setbuttonLoader(false);
   }
  token = JSON.parse(localStorage.getItem('portalUserDataToken') || '')

  return (
    <>
      <div className='pt-4 m-2'>
        <div className='card'>
          <div className='card-body py-3 '>
            <div className='row m-4 p-2'>
              <div className='col-3'>
                <div className='card-toolbar' style={{width: '100%'}}>
                  <h3 className='card-title align-items-start flex-column'>
                    <span
                      className='card-label fw-bolder fs-3 mb-1'
                      style={{color: 'rgb(138, 38, 80)'}}
                    >
                      Watchlist <>({dateTodayes})</>
                    </span>
                  </h3>
                </div>
              </div>
              <div className='col-3'></div>
              <div className='col-3'>
                <div className='text-center'> <Tooltip placement="topLeft" title={"Notify Reviewer"}>
                <Popconfirm
    title="Do you want to mark watch list as processed?"
    // description="Are you sure to delete this task?"
    onConfirm={handleNotification}
    okText="Yes"
    cancelText="No"
  >
                  <Button type="primary"   size={'large'}  loading={buttonLoader}>Notify Reviewer</Button>
                 </Popconfirm> </Tooltip></div>
              </div>
              <div className='col-3'>
                <div id='outer'>
                  <div className='inner p-1'>
                    {' '}
                    <Tooltip placement='topLeft' title={'Search'}>
                      <Button
                        type='primary'
                        size={'large'}
                        onClick={() => {
                          setIsModalOpen(true)
                        }}
                      >
                        Search
                      </Button>
                    </Tooltip>{' '}
                  </div>
                  <div className='inner p-1'>
                    <Tooltip placement='topLeft' title={'Search'}>
                      <Button
                        type='primary'
                        size={'large'}
                        onClick={() => {
                          getBuyerWatchlist()
                        }}
                      >
                        Reset
                      </Button>
                    </Tooltip>{' '}
                  </div>
                </div>
              </div>
            </div>
            <ul className='nav nav-custom nav-tabs nav-line-tabs nav-line-tabs-2x border-0 fs-4 fw-bold mb-8 main-table'>
            <li className='nav-item'>
                <a
                  className='nav-link text-active-primary pb-4 active'
                  data-bs-toggle='tab'
                  href='#Buyer'
                  onClick={() => {setActivemenu(1)
                                  setCurrentPage(1)}}
                >
                BUYER INTEL
                </a>
              </li>
              <li className='nav-item'>
                <a
                  className='nav-link text-active-primary pb-4 '
                  data-bs-toggle='tab'
                  href='#Preset'
                  onClick={() => {setActivemenu(2)
                                  setCurrentPage(1)}}
                >
                PRESET FOR OFFICE-DAILY
                </a>
              </li>

              <li className='nav-item'>
                <a
                  className='nav-link text-active-primary pb-4'
                  data-kt-countup-tabs='true'
                  data-bs-toggle='tab'
                  href='#RDS'
                  onClick={() => {setActivemenu(3)
                  setCurrentPage(1)}}
                >
                  RDS
                </a>
              </li>
              <li className='nav-item'>
                <a
                  className='nav-link text-active-primary pb-4'
                  data-kt-countup-tabs='true'
                  data-bs-toggle='tab'
                  href='#OFFICEWATCHLIST'
                  onClick={() => {setActivemenu(4)
                  setCurrentPage(1)}}
                >
                  BUYER WATCHLIST
                </a>
              </li>
              <li className='nav-item'>
                <a
                  className='nav-link text-active-primary pb-4'
                  data-kt-countup-tabs='true'
                  data-bs-toggle='tab'
                  href='#BUYERINTEL'
                  onClick={() => {setActivemenu(5)
                  setCurrentPage(1)}}
                >
                  OFFERS
                </a>
              </li>
              <li className='nav-item'>
                <a
                  className='nav-link text-active-primary pb-4'
                  data-kt-countup-tabs='true'
                  data-bs-toggle='tab'
                  href='#BUYERINTEL'
                  onClick={() => {setActivemenu(6)
                  setCurrentPage(1)}}
                >
                  PRESET FOR OFFICE - TWICE A WEEK
                </a>
              </li>
              <li className='nav-item'>
                <a
                  className='nav-link text-active-primary pb-4'
                  data-kt-countup-tabs='true'
                  data-bs-toggle='tab'
                  href='#UNDERCOSTLINE'
                  onClick={() => {setActivemenu(7)
                  setCurrentPage(1)}}
                >
                  UNDERCOST LINES
                </a>
              </li>
            </ul>
            <div className='tab-content' id='myTabContent'>
              <div className='tab-pane fade show active ' id={activeMenu} role='tabpanel'>
                <Table columns={columns} dataSource={data} loading={loader} pagination={false}
                
                onChange={(val: any, filter: any, sorter: any, extra: any) => {
                  // if(activeMenu==7)
                  //   {
                  //     setsorter2({
                  //       ...sorter,
                  //     })
                  //   }
                    // else{
                      setsorter({
                        ...sorter,
                      })
                    // }
                 
                }}
                />
                <div className='row text-end m-4'>
                  <Pagination
                    defaultCurrent={currentPage}
                    defaultPageSize={20}
                    current={currentPage}
                    total={rowCnt}
                    onChange={(e) => {
                      setCurrentPage(e)
                      // getBuyerWatchlist()
                    }}
                    hideOnSinglePage={true}
                    showSizeChanger={false}
                  />
                </div>
              </div>
              {/* <div className='tab-pane fade show active ' id='RDS' role='tabpanel'></div> */}
            </div>

            <Modal open={isModalOpen} onCancel={handleCancel} footer={false} destroyOnClose>
              <Form
                name='time_related_controls'
                // {...formItemLayout}
                onFinish={onFinish}
                style={{maxWidth: 600}}
              >
                <Form.Item name='daterange' label='From Date - To Date'>
                  <RangePicker />
                </Form.Item>
                <Form.Item name='productcode' label='Product Code'>
                  <Input placeholder='Search Product' />
                </Form.Item>
                <Form.Item>
                  <Button type='primary' htmlType='submit'>
                    Submit
                  </Button>
                </Form.Item>
              </Form>
            </Modal>
          </div>
        </div>
      </div>
    </>
  )
}
export default ViewWatchlist
